body{
  background-color: #babc9c;
}
/* Navbar.js */
.mode-img{
  padding: 8px 8px 8px 8px;
}
.navbar>.container, .navbar>.container-fluid, .navbar>.container-lg, .navbar>.container-md, .navbar>.container-sm, .navbar>.container-xl, .navbar>.container-xxl{
  justify-content:flex-start !important;
}
.navitem{
  padding-left: 15px;
}
.light-text{
  color: rgb(19, 19, 19) !important;
}
.dark-text{
  color: #C84B31 !important;
}
.navbar-brand{
  color: #E8E8A6;
}
a{
  text-decoration: none;
}
.light-btn{
  background-color: #219F94 !important;
  border: none;
  color: black !important;
}
.dark-btn{
  background-color: #C84B31 !important;
  border: none;
}
.dark-btn:hover{
  background-color: #dd5d40fd !important;
}
.light-btn:hover{
  background-color: #086d64 !important;
}
.form-switch{
  position: absolute;
  right: 0;
  padding: 72px;
}
/* TextForm.js */
.preview{
  margin-top: 5px;
}
.btn{
  border-radius: 7px;
}
.copied{
  background-color: none !important;
  background-color: #dbde9c !important;
  color: black !important;
}

@media (max-width:500px)  {
  .form-check-label {
    font-size: 1.7vh;    
  }
  .btn{
    margin-top: 1rem!important;
    margin-bottom: 1rem!important;
    margin-right: 1rem!important;
    margin-left: 1rem!important;
  }
  .form-switch {
    position: none;
    padding: 12px;
    /* padding-left: 20px !important; */
  }
}